    openVideoDialog(item) {
      this.video = item.video
      this.poster = this.$useWebpImg(item.imgUrl)
      this.showVideoDialog = true
    },
    handleClick(item, isOpenLink = true) {
      if (item.link && isOpenLink) {
        this.$router.push(item.link)
      } else if (item.imgList) {
        this.openImgDialog(item)
      } else if (item.video) {
        this.openVideoDialog(item)
      }
    }
